module.exports = [{"id":"docker-step-by-step","name":"《第一次用Docker就上手》","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":"http://books.gotop.com.tw/v_ACA023400"},"languages":null,"frameworks":null,"description":["本書原本為《循序漸進學 Docker》，是中國出版的書籍。碁峰資訊請我協助審校，將中國的慣用術語轉成台灣在地的慣用語。也因為原作出版已有段時間，Docker 在發展又算快速，裡面有許多資訊都過期了，在審校時我也協助修正這些過期的資訊。"],"screenshots":["./docker_book.jpg"]},{"id":"korat","name":"KORAT（基於影像擷取卡的錄製/重播工具）","from":"2015-03-06","to":"2016-03-09","links":{"source":null,"demo":"https://www.youtube.com/watch?v=Oam0kdAYIok&t=42s"},"languages":["C#"],"frameworks":[".NET Framwork 4.5"],"description":["A capture/replay testing tool. Based on C# with .NET Framework.","I redesign user interface with Metro UI. Customer consider it is more intuitive and professional than before. I also architecturally refactor the GUI program. It decreased the maintaining cost and coupling between relative components."],"screenshots":null},{"id":"nine-grids-judge","name":"Nine Grids Judge System (九宮格審題系統)","from":"1970-01-01","to":"1970-01-01","links":{"source":"https://github.com/fntsrlike/nine-grids-judge","demo":"http://compiler.oolab.fntsr.tw"},"languages":["Ruby","JavaScript","HTML","CSS"],"frameworks":["Ruby on Rails","SemanticUI"],"description":["一個用於翻轉教育的審題系統，由助教新建章節、題目，讓學生可以挑戰每一個章節中隨機產生的九道題目。提交的答案會交由助教審核是否通過，並給予建議。只要能讓位於九宮個的九道題目連成一條線，該章節就會通過。由於流程都電子化了，所以可以簡單整理出數據，幫助助教追蹤學生學習狀況，以及題目難易調整。","概念由中央大學鄭永斌副教授提出，並應用在其教授的編譯器課程。目前正接受學生與助教的回饋，持續改進中。希望此專案未來穩定後，能推廣更多學校課程使用。"],"screenshots":["./ng_1_o.png","./ng_2_o.png","./ng_3_o.png","./ng_4_o.png"]},{"id":"ics2015","name":"Conference Registry and Paper Review System（論文投稿與審核系統）","from":"2015-07-24","to":"2016-01-05","links":{"source":null,"demo":"http://163.17.100.89"},"languages":["Ruby"],"frameworks":["Ruby on Rails"],"description":["本系統為「2016 運動生物力學研討會暨臺灣運動生物力學學會年會（ICS2015）」的論文投稿、審核系統，透過本系統得以讓要參與年會的教授與研究生投稿，再由管理員分配稿件給各相關領域的評審委員，最後在評審委員給出分數與評論後，交由管理員審核是否接受該篇論文，並通知投稿者審稿結果。"],"screenshots":null},{"id":"ncyu-life","name":"An-Xin-Zai-Jia (安心在嘉)","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":null},"languages":["JavaScript"],"frameworks":["Vue.js 1.x, Firebase"],"description":["這是一個用來宣導自殺防治的相關網站，案主的希望能有用成內容管理器，讓他們自行增修內容，但又不太希望負擔伺服器維護費用，所以這個專案我使用 GitHub Pages 作為 Host，搭配 Firebase 做為資料庫，以達成案主的需求。","案主在事後誇讚介面很好上手，讓他們行政人員不太需要花時間去學習，就能簡單的使用該網站。"],"screenshots":null},{"id":"meder_book","name":"MeDer Book（臉書塗鴉牆解析再製程式）","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":null},"languages":["Ruby"],"frameworks":["Ruby on Rails"],"description":[""],"screenshots":null},{"id":"morse-code","name":"Morse Code（摩斯電碼編解碼工具）","from":"1970-01-01","to":"1970-01-01","links":{"source":"https://github.com/fntsrlike/morse.fntsr.tw","demo":"https://morse.fntsr.tw"},"languages":["JavaScript"],"frameworks":["Vue.js 1.x, BULMA"],"description":["A morse code encode/decode online tool. It can generate sound of morse codes more efficiently than other online tools. Based on Vue.js, RifeWave.js and BULMA CSS. framework."],"screenshots":null},{"id":"sms-edu","name":"Echelon Quota Table of Education of SMS（教育役各梯開缺解析工具）","from":"2016-11-06","to":"2016-11-06","links":{"source":"https://github.com/fntsrlike/A-Ti-at-Schools","demo":"https://goo.gl/3CBxfw"},"languages":["Ruby"],"frameworks":null,"description":["這是用來解析教育替代役歷梯次有哪些學校有開缺的小工具，用來輔助推測最近又會有哪些學校有開缺，以方便準備事前學校情報的相關功課。","這個小工具在我確定要服教育替代役時，利用成功嶺軍事訓練和教育役專業訓練之間的週末寫的。並將解析出來的 csv 檔轉成 Google Sheet，搭配其格式上色功能，完成一個較易閱讀的文件，讓我與同梯弟兄得以了解從 72 梯到 169 梯的開缺紀錄，也因此得以做足充分情報收集，選到自己偏好的服勤處所。"],"screenshots":["./a-ti.png"]},{"id":"slack-irc-syncbot","name":"Slack IRC Syncbot (Slack IRC 訊息同步機器人)","from":"1970-01-01","to":"1970-01-01","links":{"source":"https://github.com/fntsrlike/slack-irc-syncbot","demo":null},"languages":["JavaScript"],"frameworks":["node.js"],"description":["Slack 是一個新興的團隊通訊平台、IRC 是一個歷史悠久無法放棄的聊天平台，兩種平台各有優劣，讓人無法取捨。此專案就是為了讓兩者能夠各展所長，因此寫了機器人同步兩邊的機器人，讓彼此的愛用者不必去遷就對方，仍可以互相溝通、討論。","此專案目前運用在 CHILDISH、SDNDS-TW、PHP Taiwan Community，並持續中改進中。希望能藉由這個專案，讓前輩繼續慣用 IRC、新手快速入門 Slack 並進行雙方交流與討論。"],"screenshots":["./sync_1_o.png","./sync_2_o.png"]},{"id":"isr2013","name":"ISR 2013: Burning Civilization","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":null},"languages":["Lua"],"frameworks":["Love"],"description":[""],"screenshots":null},{"id":"npps-59-607","name":"NPPS 59 607（內埔國小五十九屆六年庚班班級網頁 復刻版）","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":null},"languages":["JavaScript","HTML","CSS"],"frameworks":null,"description":[""],"screenshots":null},{"id":"board-manger-system","name":"Board Mangement System (公佈欄管理系統)","from":"1970-01-01","to":"1970-01-01","links":{"source":"https://github.com/fntsrlike/boards_manager","demo":"http://app.nchusg.org/poster_manager"},"languages":["PHP"],"frameworks":["Laravel"],"description":["海報公佈欄的管理，以往都是手動填表，然後人工評斷是否額度正常、重複申請等許多審核細節，若是稍有不慎，就會造成張貼單位重複、爭吵，更別體在審查的過程中，看著密密麻麻的列表，是多麽得耗時費力。於是寫出一個電子化的系統協助處理公佈欄張貼的勢，是本專案存在的目的。","本專案目前使用在國立中興大學學生會，用來管理學生張貼海報相關事宜。"],"screenshots":["./bms.jpg"]},{"id":"appeal-management-system","name":"Appeal Management System (案件管理系統)","from":"1970-01-01","to":"1970-01-01","links":{"source":"https://github.com/fntsrlike/appeal_system","demo":null},"languages":["PHP"],"frameworks":["Laravel"],"description":["案件管理一直是滿常被提出的一種內容管理需求，雖然網路上已經有很多類似的專案，但細節盡不相同。而本專案偏向比較重個人、隱私性的方向，像是權利申訴、維修單回報等，屬於個人對組織的回報，但仍提供兩者持續性的溝通，並在解決後決定是否公開。","本案件是為了學生會的學權申訴所寫，目前仍未上線。"],"screenshots":["./appeal.jpg"]},{"id":"nchusg-official","name":"NCHUSG Official Website (國立中興大學學生會網站)","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":"https://nchusg.org"},"languages":["PHP"],"frameworks":["Laravel"],"description":["一個學生會網站應該有什麼特色，要如何在一個粉絲專頁橫行的時代，凸顯官方網站的重要性與地位，這是當初任職學生會資訊長不斷思考的議題。以簡潔的設計，提供正式公告閱讀、行事曆與連絡資料，簡單卻重要的資訊。","並且將官方網站定位成一個學生入口網站，以此為中心開發各式網路應用程式。例如以 Wordpress 搭配套件將公告放在官網上、提供各式學生 App 的連結，讓每項服務即App，單獨維護，卻又利用官網為中心的特性，彙整資料。","目前此概念以作為學生會資訊部開發官網的方針，由後屆學弟妹持續維護中。"],"screenshots":["./nchusg_o.png"]},{"id":"business-statics-system","name":"Business Statics System (業務統計系統)","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":null},"languages":["PHP"],"frameworks":["Customize PHP MVC framework"],"description":["每年公家機管總是要統計各種資料，然後對使用者公佈。在以往的習慣中，以 Email 發送微軟的試算表檔案，由各單位填寫後回信，再交由總管單位去匯整，充斥的不便與瑣碎。本專案即為此需求所開發，由總管單位建立各單位必須填寫的欄位，然後每月定期填寫數據。在另外設計表格，去讀取該欄位的資料，不用會 SQL，只需使用簡單的 HTML 標籤與屬性，專案就會自動幫忙填寫，搞定一切。","本專案用於國立中興大學圖書館中，為本人在旗下任職工讀生時所寫。現由圖書館資訊組做後續維護。"],"screenshots":["./fss_o.png"]},{"id":"book-search-system","name":"Book Search System (尋書系統)","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":null},"languages":["PHP"],"frameworks":null,"description":["讀者在書架上找不到書，只好向櫃檯索取申請單，填寫相關資料，並由服務人員去追蹤。但這些流程有許多是程式可以取代的，一套尋書系統也是圖書館必備的網路應用程式，幫助圖書館主管去追蹤現有尋書狀況、進度，並且能讓讀者知道第一手的詢書訊息，增加官方與讀者的交流與信任。","本專案主要是協助重構原本向其他單位交流所得的程式，改寫成現有單位適合的流程。且將原本重覆性極高的程式，轉換成函式與模組，增強重用性。"],"screenshots":null},{"id":"room-borrow-system","name":"Room Borrow System (研究小間、讀者討論室借用系統)","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":"http://class.lib.nchu.edu.tw/lib/order.php"},"languages":["PHP"],"frameworks":["none"],"description":["空間借用也是很常見的內容管理專案程式，將空間借用的流程電子化，省去可以交由電腦審核的部分，並且搭配網路去控管空間的開關與使用。","在本專案主要是協助館方現有專案的維護與新增功能，也是本人的第一個案子，更是學習使用 PHP 的起點。雖然現在能力已經有辦法實作更好用的系統，但當初維護此系統，讓我入門的收益，是永遠無法抹滅的。"],"screenshots":["./rbs_o.png"]},{"id":"nf-bbs","name":"New Fantasy Field, BBS of WHSH (國立文華高中 BBS 校站)","from":"1970-01-01","to":"1970-01-01","links":{"source":null,"demo":null},"languages":["C"],"frameworks":["Maple-Itoc"],"description":["對現在年輕人提及 BBS，大概已經一問三不知，除了 PTT 以外的 BBS 幾近沒落，但 BBS 卻是我高中以前，最活躍的網路平台，幾乎每個學校都會維護一個 BBS 站，也是許多人在學生時代的美好回憶。","在我高一入學時，也是學校 BBS 站沒落且關閉的時期。任職於電研社社長的時候，就努力想嘗試在復站，最後在硬碟資料已經找不會的情況下放棄，但轉而重新架設一個的方向。最後終於在指考前三個月重新架起。儘管現在學校不再支持硬體去提供服務，但此專案也算是我在高中學習程式設計和系統管理的一個起點，也是讓我享受程設設計喜悅的開端。"],"screenshots":["./nf_o.png"]}]

